.line {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30px;
  height: 2px;
  background-color: #2c39f2;
  position: relative;
  transition: transform 0.5s ease-out; }
  .line::before {
    content: '';
    width: 30px;
    height: 2px;
    display: block;
    background-color: #2c39f2;
    position: absolute;
    top: -8px;
    transform-origin: top center;
    transition: transform 0.5s ease-out; }
  .line::after {
    content: '';
    width: 30px;
    height: 2px;
    display: block;
    background-color: #2c39f2;
    position: absolute;
    bottom: -8px;
    transform-origin: top center;
    transition: transform 0.5s ease-out; }
  .line.active {
    width: 30px;
    background-color: #ffffff; }
    .line.active::before {
      transform: rotate(-45deg) translate(-7px, 5px); }
    .line.active::after {
      transform: rotate(45deg) translate(-5px, -6px); }

