@keyframes glow {
  0% {
    opacity: 0.2; }
  30% {
    opacity: 1; }
  60% {
    opacity: 1; }
  100% {
    opacity: 0.2; } }

.animatedFromTop {
  opacity: 0;
  transform: translate(0, -100px);
  animation: revealFromTop 0.3s 0.9s ease-in forwards; }

.animatedFromLeft {
  opacity: 0;
  transform: translateX(-100%);
  animation: revealFromLeft 0.3s 0.9s ease-in forwards; }

.animatedFromRight {
  opacity: 0;
  transform: translateX(100%);
  animation: revealFromRight 0.3s 0.9s ease-in forwards; }

.animatedFromBottom {
  opacity: 0;
  transform: translate(-50%, 50%);
  animation: revealFromBottom 0.3s 0.9s ease-in forwards; }

@keyframes revealFromTop {
  from {
    opacity: 0;
    transform: translate(0, -100px); }
  to {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes revealFromBottom {
  from {
    opacity: 0;
    transform: translate(-50%, 50%); }
  to {
    opacity: 1;
    transform: translate(-50%, 0%); } }

@keyframes revealFromLeft {
  from {
    opacity: 0;
    transform: translateX(-100%);
    filter: blur(10px); }
  to {
    opacity: 1;
    transform: translateX(0%);
    filter: blur(0px); } }

@keyframes revealFromRight {
  from {
    opacity: 0;
    transform: translateX(100%);
    filter: blur(10px); }
  to {
    opacity: 1;
    transform: translateX(0%);
    filter: blur(0px); } }

.animatedLogoLeft {
  opacity: 0;
  transform: translateX(50px) scale(0);
  filter: blur(30px);
  transform-origin: top center;
  animation: logoRevealLeft 1.5s .3s ease-out forwards; }

.animatedLogoRight {
  opacity: 0;
  transform: translateX(-50px) scale(0);
  filter: blur(30px);
  transform-origin: top center;
  animation: logoRevealRight 1.5s .6s ease forwards; }

@keyframes logoRevealLeft {
  0% {
    transform: translateX(45px) scale(0);
    filter: blur(10px); }
  50% {
    transform: translateX(45px) scale(1);
    filter: blur(0px);
    opacity: 1; }
  60% {
    transform: translateX(45px) scale(1);
    filter: blur(0px);
    opacity: 1; }
  100% {
    transform: translateX(0) scale(1);
    filter: blur(0);
    opacity: 1; } }

@keyframes logoRevealRight {
  0% {
    transform: translateX(-45px) scale(0);
    filter: blur(30px); }
  10% {
    transform: translateX(-45px) scale(1);
    opacity: 0; }
  100% {
    transform: translateX(0) scale(1);
    filter: blur(0);
    opacity: 1; } }

@keyframes revealSvgPath {
  from {
    opacity: 0;
    transform: translate(-10px, 30px); }
  to {
    transform: translate(0px, 0px);
    opacity: 1; } }

.animateDinoPicture {
